:root {
    --green: #2453B0;
    --black87: rgba(0, 0, 0, 0.87);
    --textHea: #2453b0;
}

html,
body {
    width: 100%;
    height: 100%;
    background-color: white;
    font-size: 16px;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    margin: 0;
    overflow-y: unset;
    position: relative;
    text-rendering: optimizeLegibility;

    >* {
        height: 100%;

        >* {
            height: 100%;
        }
    }
}

a {
    display: block;
    // color: var(--green);
    font-size: 16px;
    transition: 0.3s all ease-in-out;
    color: #333;

    &:hover {
        color: var(--green);
    }
}

.text-white {
    color: white;
}

.m-center {
    flex-grow: 1;
    display: flex;
    align-items: center;
    margin: auto;
}

.main-center {
    margin: auto;
    height: 100%;

    &.arrow_right>li>.collapsible-header {
        // background-color: var(--green);
        // color: white; //#fbf7ea
    }
}

.icon {
    color: #bdcbe7;
}

.main-center>li>.collapsible-body {
    background-color: #fbf7ea;

}

.max-1024 {
    max-width: 1024px;
    margin: auto;
}

.bg-green {
    background-color: var(--green);
    color: whitesmoke;
}

.footer {
    background-color: #232f3c;
    padding: 23px 16px;
}

.home {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    //  margin-left: 16px;
}

.main {
    flex-grow: 1;
}

.faq {
    border: 2px solid var(--green);
    border-radius: 9999px;
    background-color: white;
    color: var(--green);
    display: inline-flex;
    align-items: center;
}

.faq:hover {
    background: white;
    opacity: 0.9;
    color: var(--black87);
    //  border: 2px solid var(--black87);
}

.detail {
    padding: 16px;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    text-align: left;
    max-width: 910px;
    margin: auto;

}

.title {
    font-size: 26px;
}

.linkApp {
    color: #39b3ed;
    line-height: 26px;
}

.divider {
    display: block;
    width: 100%;
}

.margin-bottom-24 {
    margin-bottom: 24px;
}

.margin-top-24 {
    margin-top: 24px;
}

.content {
    &>ul {
        margin-left: 36px;
    }

    &>li {
        list-style-type: square !important;
        padding: 16px;
    }
}

.content ul:not(.browser-default)>li {
    list-style-type: square !important;
    padding: 12px;
}

.max-1024 {
    padding-top: 0px;
}

.collapsible-body>div>a {
    margin-top: 12px;
}

.material-icons {
    transition: 0.4s ease-in;
    transform: rotate(0deg);
    color: #bdcbe7;
    ;
    font-size: 40px !important;
}

.arrow_right>*>.collapsible-header {
    transition: 0.3s ease-in;
    color: var(--green);

}

.arrow_right>.active>.collapsible-header {
    background-color: var(--green);
    color: white; //#fbf7ea
}

.collapsible-header {
    display: flex;
    align-items: center;
    padding: 12px !important;
}

li:not(.active)>.collapsible-header>.material-icons {
    color: #bdcbe7 !important;
    transform: rotate(0deg);
}

li.active>.collapsible-header.active>.material-icons {
    transform: rotate(90deg);
    color: white;
}

.collapsible-body>div {
    padding-left: 32px;
}

.collapsible-body {
    margin: 0;
    padding-bottom: 16px;
    padding-top: 8px;
}

.collapsible-header {
    font-size: 19px;
    font-weight: 400;
}

.logo {
    margin-left: 16px;
}

.sidenav-overlay {
    z-index: 100;
}

.p {
    margin: 0;
}

@media(min-width:1240px) {
    .d-sm {
        display: none;
    }
    
}
@media(max-width:1024px){
    .padding-left-16-sm{
        padding-left: 16px;
    }
}
.light:hover {
    color: rgba(255, 255, 255, 0.86);
}

.collapsible-header:hover {
    opacity: 0.9;
    background-color: rgba(0, 0, 0, 0.05);
}

.video-if {
    overflow: hidden;
    height: 100%;
    width: 100%;
    min-height: 400px;
}

.video {
    width: 100%;
}

.btn-menu {
    padding-left: 16px !important;
}

@media(max-width:768px) {
    .light {
        padding-left: 16px !important;
    }
}

.nvbart>li>div>div.divider {
    margin-top: 0;
}

@media(max-width:768px){
    .text-green {
        color: var(--green);
        font-weight: 400;
    }
}